.MyNavBar {
  width: 100%;
  height: 80px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  justify-content: center;
  display: flex;
}
.MyLink-Top {
  padding: 10px;
  cursor: pointer;
  height: 40px;
  display: flex;
  justify-content: center;
  position: relative;
}

.MyLink-Top:after {
  content: "";
  position: absolute;
  display: inline-block;
  bottom: 0;
  width: 0%;
  height: 2px;
  margin-top: 12px;
  background-color: black;
  transition: width 0.3s ease;
}
.MyLink-Top:hover:after {
  width: 100%;
}
.rotateImage {
  transform: rotate(0deg);
  transition: all 1s ease-in-out;
}

.rotateImage:hover {
  transform: rotate(360deg);
}
